'use strict';

import {RedocComponent, BaseComponent} from '../base';
import {Tabs, Tab} from '../../common/components/Tabs/tabs';
import {ElementRef} from 'angular2/core';
import JsonPointer from '../../utils/JsonPointer';

@RedocComponent({
  selector: 'json-schema',
  template: `
    <span *ngIf="isTrivial" class="param-wrap">
      <span class="param-type param-type-trivial {{type}}"
      [ngClass]="{'with-hint': _displayTypeHint}" title="{{_displayTypeHint}}">{{_displayType}}</span>
    </span>
    <div *ngIf="!isTrivial" class="params-wrap" [ngClass]="{'params-array': isArray}">
      <div *ngFor="#prop of data.properties" class="param-wrap">
        <div class="param" [ngClass]="{'discriminator': prop.isDiscriminator}">
          <div class="param-name">
            <span>{{prop._name}}</span>
          </div>
          <div class="param-info">
            <div>
              <span class="param-type {{prop.type}}" [ngClass]="{'with-hint': prop._displayTypeHint}"
              title="{{prop._displayTypeHint}}"> {{prop._displayType}} {{prop._displayFormat}}</span>
              <span *ngIf="prop.isRequired" class="param-required">Required</span>
            </div>
            <div class="param-description" innerHtml="{{prop.description | marked}}"></div>
            <div class="discriminator-info" *ngIf="prop.isDiscriminator"> This field value determines the exact schema: </div>
          </div>
        </div>
        <div class="param-schema" [ngClass]="{'param-array': prop._isArray}" *ngIf="prop._pointer">
          <json-schema pointer="{{prop._pointer}}" [isArray]='prop._isArray'>
          </json-schema>
        </div>
      </div>
      <div *ngIf="data.derived.length" class="param-wrap">
        <tabs>
          <tab *ngFor="#derived of data.derived" tabTitle="{{derived.name}}">
            <json-schema pointer="{{derived.$ref}}" [final]="derived.final">
            </json-schema>
          </tab>
        </tabs>
      </div>
    </div>
  `,
  styles: [`
    .param-schema{padding-left:12.5px;border-left:1px solid #7D97CE}.param-wrap{position:relative}.param-schema:before{content:"";position:absolute;left:13.5px;top:20px;bottom:0;border-left:1px solid #7D97CE}.param-name{font-size:14px;padding:10px 25px 10px 0;font-weight:bold;box-sizing:border-box;line-height:20px;border-left:1px solid #7D97CE;white-space:nowrap;position:relative}.param-info{width:100%;padding:10px 0;box-sizing:border-box;border-bottom:1px solid #ccc}.param{display:flex}.param-required{color:red;font-weight:bold;font-size:12px;line-height:20px;vertical-align:middle}.param-type{color:#999;font-size:12px;line-height:20px;vertical-align:middle;font-weight:bold}.param-type.array:before{content:"Array of ";color:#999}.param-type.string{color:rgba(0,80,0,0.7)}.param-type.integer,.param-type.number{color:rgba(74,139,179,0.8)}.param-type.object{color:rgba(0,50,159,0.7)}.param-type.boolean{color:firebrick}.param-type.with-hint{background-color:rgba(0,50,159,0.1);padding:0.2em 0;font-size:0.85em;border-radius:3px;cursor:help}.param-type.with-hint:before,.param-type.with-hint:after{content:"\\00a0"}.param-type-trivial{margin:10px 10px 0;display:inline-block}.param-name>span:before{content:"";display:inline-block;width:7px;height:7px;background-color:#7D97CE;margin:0 10px;vertical-align:middle}.param-name>span:after{content:"";position:absolute;border-top:1px solid #7D97CE;width:10px;left:0;top:20px}.param-wrap:first-of-type>.param>.param-name:before{content:"";display:block;position:absolute;left:-1px;top:0;border-left:2px solid #fff;height:20px}.param-wrap:last-of-type>.param>.param-name{position:static}.param-wrap:last-of-type>.param>.param-name:after{content:"";display:block;position:absolute;left:-1px;border-left:2px solid #fff;top:21px;background-color:white;bottom:0}.param-wrap:last-of-type>.param-schema{border-left-color:transparent}.param-schema .param-wrap:first-of-type .param-name:before{display:none !important}.params-wrap.params-array:before,.params-wrap.params-array:after{display:block;font-weight:bold;color:#999;font-size:12px;line-height:1.5}.params-wrap.params-array:after{content:"]"}.params-wrap.params-array:before{content:"Array [";padding-top:1em}.params-wrap.params-array{padding-left:10px}.param-schema.param-array:before{bottom:9px;width:10px;border-left-style:dashed;border-bottom:1px dashed #7D97CE}.params-wrap.params-array>.param-wrap:first-of-type>.param>.param-name:after{content:"";display:block;position:absolute;left:-1px;top:0;border-left:2px solid #fff;height:20px}.params-wrap>.param>.param-schema.param-array{border-left-color:transparent}.param.discriminator>div{padding-bottom:0;border-bottom:0}.discriminator-info{font-weight:bold}:host tabs{display:block;border-left:1px solid #7D97CE}:host tabs li{margin:0.2em 0.5em 0.2em 0;font-size:14px;border:0;color:white;padding:0 15px;border-radius:10px;background-color:#8A9094}:host tabs li:last-of-type{margin:0}:host tabs li.active{background-color:#00329F}
  `],
  directives: [JsonSchema, Tabs, Tab],
  inputs: ['isArray', 'final']
})
export default class JsonSchema extends BaseComponent {
  constructor(schemaMgr, elementRef) {
    super(schemaMgr);
    this.element = elementRef.nativeElement;
    this.final = false;
  }

  prepareModel() {
    this.data = {};
    this.data.properties = [];
    this.data.derived = [];

    if (!this.componentSchema) {
      throw new Error(`Can't load component schema at ${this.pointer}`);
    }

    this.dereference();
    let schema = this.componentSchema;

    if (schema.type === 'array') {
      this.isArray = true;
      if (schema._pointer) {
        this.pointer = JsonPointer.join(schema._pointer, 'items');
      }
      schema = schema.items;
    }
    let normPtr = schema._pointer || this.pointer;
    let derived = this.schemaMgr.findDerivedDefinitions( normPtr );
    if (!this.final && derived.length) {
      this.data.derived = derived;
      this.data.discriminator = schema.discriminator;
    }

    this.joinAllOf(schema, {omitParent: true});

    if (schema.type !== 'object') {
      this.isTrivial = true;
      this._displayType = schema.type;
      if (schema.format) this._displayType = `${this.displayType} <${schema.format}>`;
      return;
    }

    this.pointer = schema._pointer || this.pointer;

    this.requiredMap = {};
    if (this.schema.required) {
      this.schema.required.forEach(prop => this.requiredMap[prop] = true);
    }

    if (!schema.properties) {
      this.isTrivial = true;
      this._displayType = schema.type;
      this._displayTypeHint = 'This field may contain data of any type';
      return;
    }

    let discriminatorFieldIdx = -1;
    let props = Object.keys(schema.properties).map((prop, idx) => {
      let propData = schema.properties[prop];
      propData = this.injectPropData(prop, propData, schema);
      if (propData.isDiscriminator) discriminatorFieldIdx = idx;
      return propData;
    });
    // Move discriminator field to the end of properties list
    if (discriminatorFieldIdx > -1) {
      let discrProp = props.splice(discriminatorFieldIdx, 1);
      props.push(discrProp[0]);
    }
    this.data.properties = props;
  }

  adjustNameColumnWidth() {
    // TODO handle internal schemes differently
    let names = [].slice.call(this.element.querySelectorAll('.param-name'));
    let widths = names.map(el => el.offsetWidth);
    let maxWidth = Math.max(...widths);
    if (!maxWidth) return;
    names.forEach(el => {
      el.style.minWidth = maxWidth + 'px';
    });

    let discrValues = this.element.querySelector('tabs ul');
    if (discrValues) discrValues.style.paddingLeft = maxWidth + 'px';
  }

  injectPropData(prop, propData, schema) {
    propData = Object.assign({}, propData);
    propData._name = prop;
    propData.isRequired = this.requiredMap[prop];
    propData._displayType = propData.type;
    propData.isDiscriminator = (schema.discriminator === prop);
    if (propData.type === 'array') {
      let itemType = propData.items.type;
      let itemFormat = propData.items.format;
      if (itemType === 'object' || !itemType) {
        itemType = propData.items.title || 'object';
        propData._pointer = propData.items._pointer || JsonPointer.join(this.pointer, ['properties', prop, 'items']);
      }
      propData._displayType = `${itemType}`;
      propData.format = itemFormat;
      propData._isArray = true;
      propData.type = 'array ' + propData.items.type;
    }

    if (propData.type === 'object') {
      propData._displayType = propData.title || 'object';
    }

    if (!propData.type) {
      propData._displayType = '< * >';
      propData._displayTypeHint = 'This field may contain data of any type';
    }

    if (propData.format) propData._displayFormat = `<${propData.format}>`;

    return propData;
  }

  init() {
    setTimeout(() => this.adjustNameColumnWidth());
  }
}
JsonSchema.parameters = JsonSchema.parameters.concat([[ElementRef]]);
